html,
body {
  overflow: hidden;
}

button {
  background: transparent;
  border: 0px;
  padding: 0px;
  margin: 0px;
  text-align: left;
  cursor: pointer;
  line-height: 1 !important;
}

.flex {
  display: flex;
}

.grow {
  flex-grow: 1;
}

.spaceBetween {
  justify-content: space-between;
  align-items: center;
}

html .tabs .content {
  padding-top: 0px;
}

html .btn {
  display: inline-flex !important;
  justify-content: center !important;
  align-items: center;
}

.flex.center {
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.cell {
  min-height: 40px !important;
}

.thead-box {
  min-height: 30px !important;
}

th {
  border-radius: 0px !important;
  border-left: 0px !important;
  border-right: 0px !important;
  border-top: 0px !important;
}

html .radio .name {
  font-weight: 400 !important;
}

html .tabs .tab {
  font-size: 14px;
  padding-left: 10px;
  padding-right: 10px;
}

.titlebar {
  -webkit-user-select: none;
  -webkit-app-region: drag;
  height: 22px;
  margin-bottom: -5px;
}

.relative {
  position: relative;
  z-index: 1;
}

.padding-row > * {
  margin-left: 15px;
}

html .btn-group .select {
  border-left: 0px;
  border-top: 0px;
  border-bottom: 0px;
  border-radius: 0px;
  height: 26px;
}

html .monaco-editor .margin,
html .monaco-editor,
html .monaco-editor-background,
html .monaco-editor .inputarea.ime-input {
  background-color: transparent;
}

html .cell {
  font-size: 12px;
}

html .popover .item {
  padding: 0px 20px !important;
}

html .tooltip-content {
  overflow: hidden;
}

html .btn-dropdown button {
  min-width: 0;
}

html .btn-dropdown .content {
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
}
